import { Injectable } from '@angular/core';
import { Clients } from './clients';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ClientsService {

  constructor(private http: HttpClient){ }

  getClient(){
    return this.http.get<Clients[]>('http://localhost/Api/show.php');
  }
  createClient(client: Clients){
    return this.http.post('http://localhost/Api/add.php', client,{responseType: 'text'});
  }
  deleteClient(ID:number){
    return this.http.delete<Clients[]>('http://localhost/Api/delete.php?ID=' + ID);
  }
  getClientById(ID:number){
    return this.http.get<Clients[]>('http://localhost/Api/ShowById.php?ID=' + ID);
  }
  updateClient(clients: Clients){
    return this.http.put('http://localhost/Api/modifer.php' + '?ID=' +clients['ID'] ,clients);
  }
    
}
