import { Component, OnInit } from "@angular/core";
import { ClientsService } from "../clients.service";
import { Clients } from "../clients";
import { Router } from "@angular/router";

@Component({
  selector: "app-show",
  templateUrl: "./show.component.html",
  styleUrls: ["./show.component.css"]
})
export class ShowComponent implements OnInit {
  clients: Clients[];
  Text: string;
  constructor(private clientService: ClientsService, private router: Router) {}

  //List
  ngOnInit(): void {
    this.clientService.getClient().subscribe((data: Clients[]) => {
      this.clients = data;
    });
  }

  //Delete
  delete(clients: Clients): void {
    this.clientService.deleteClient(clients["ID"]).subscribe(data => {
      this.clients = this.clients.filter(u => u !== clients);
    });
  }

  //UPDATE
  ID: any;
  update(clients: Clients) {
    this.ID = clients["ID"];
    this.router.navigate(["Modify/" + this.ID]);
  }

  //Filter & Search
  SelectedRadio: any;
  radios: any = ["Nom", "Prenom", "Age"];

  RadioChangerHandler(event: any) {
    this.SelectedRadio = event.target.value;
  }
  search() {
    if (this.Text != "") {
      this.clients = this.clients.filter(res => {
        return res[this.SelectedRadio].match(this.Text);
      });
    } else if (this.Text == "") {
      this.ngOnInit();
    }
  }
}
